body {
  background-image: linear-gradient(to bottom, #af67e9, #6565e7);
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed; }

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 15rem; }

.faq {
  display: block;
  position: relative;
  width: 85%;
  background-color: #e7e7e9;
  border-radius: 25px; }
  @media (min-width: 1280px) {
    .faq {
      display: grid;
      gap: 0;
      grid-template-columns: repeat(2, 1fr);
      width: 65%;
      align-items: center;
      justify-content: center; } }
  .faq__image {
    position: relative;
    top: -7rem;
    z-index: 2;
    background-image: url("../img/illustration-woman-online-mobile.svg");
    background-size: 70%;
    background-position: 35% 0%;
    background-repeat: no-repeat;
    height: 20rem;
    width: 100%;
    margin-bottom: 3rem; }
    @media (min-width: 768px) {
      .faq__image {
        top: -10rem;
        margin: 0 auto;
        width: 85%;
        height: 40rem;
        background-position: 35% 20%; } }
    @media (min-width: 1280px) {
      .faq__image {
        top: 0;
        left: 0;
        background-image: url("../img/illustration-woman-online-desktop.svg");
        background-size: 110%;
        background-position: 120% center;
        height: 100%;
        width: 100%; } }
    @media (min-width: 1280px) {
      .faq__image::after {
        content: '';
        left: -5rem;
        z-index: 1;
        position: absolute;
        background-image: url("../img/illustration-box-desktop.svg");
        background-size: 40%;
        background-position: -15% 65%;
        background-repeat: no-repeat;
        height: 100%;
        width: 100%; } }
    .faq__image::before {
      content: '';
      z-index: 1;
      bottom: -2rem;
      position: absolute;
      background-image: url("../img/bg-pattern-mobile.svg");
      background-size: 75%;
      background-position: 45% 45%;
      background-repeat: no-repeat;
      height: 100%;
      width: 100%; }
      @media (min-width: 768px) {
        .faq__image::before {
          bottom: 1rem;
          background-size: 65%;
          background-position: 45% 75%; } }
      @media (min-width: 1280px) {
        .faq__image::before {
          bottom: 0;
          background-image: url("../img/bg-pattern-desktop.svg");
          background-size: 85%;
          background-position: 45% 85%;
          height: 100%;
          width: 100%; } }
  .faq__info {
    padding: 2rem;
    margin-top: -15rem; }
    @media (min-width: 768px) {
      .faq__info {
        padding: 2rem 8rem 4rem 8rem; } }
    @media (min-width: 1280px) {
      .faq__info {
        padding: 4rem 8rem 4rem 6rem;
        margin-top: 0; } }
    .faq__info h1 {
      margin: 0;
      margin-bottom: 1.5rem;
      font-weight: 700;
      text-align: center; }
      @media (min-width: 768px) {
        .faq__info h1 {
          margin-bottom: 2.5rem; } }
      @media (min-width: 1280px) {
        .faq__info h1 {
          margin-bottom: 2.5rem;
          text-align: left; } }
    .faq__info p {
      font-size: 1.3rem; }

.q_a ul {
  list-style: none;
  padding: 0; }
  .q_a ul li .question {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .q_a ul li .question p {
      font-weight: 400;
      margin: 0;
      color: #000000;
      transition: all 150ms ease-in-out; }
      .q_a ul li .question p:hover {
        color: #f47c57; }
    .q_a ul li .question img {
      height: 1.5rem;
      width: auto;
      transform: rotate(0);
      transition: transform 350ms ease-in-out; }
      .q_a ul li .question img:hover {
        cursor: pointer; }
  .q_a ul li span {
    content: "";
    display: block;
    width: 100%;
    height: .05rem;
    margin-top: .75rem;
    background-color: #c9c9cf;
    border-radius: 50px;
    margin-bottom: 2rem; }
  .q_a ul li .answer {
    display: none;
    font-weight: 400;
    line-height: 1.4;
    transition: opacity 350ms ease-in-out; }
  .q_a ul li.open .question p {
    font-weight: 700; }
  .q_a ul li.open .question img {
    transform: rotate(180deg); }
  .q_a ul li.open .answer {
    display: block; }

.attribution {
  margin: 1rem 0;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-align: center; }
  .attribution a:hover {
    color: #f47c57; }

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0; }
  1% {
    visibility: visible;
    opacity: 0; }
  to {
    visibility: visible;
    opacity: 1; } }

.fade-in {
  animation: fade-in 350ms ease-in-out forwards; }

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1; }
  99% {
    visibility: visible;
    opacity: 0; }
  to {
    visibility: hidden;
    opacity: 0; } }

.fade-out {
  animation: fade-out 350ms ease-in-out forwards; }
